#houseWidget {
    display: grid;
    grid-template-rows: 1fr 9fr;
    border: 1px solid black;
    border-radius: 30px;
}
#houseWidgetButtons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
}
#houseWidgetButtons div {
    border: 1px solid black;
}